import React, {FC, useEffect, useState} from 'react';

const ProfilePage:FC = () => {
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    setLoading(false);
  });

  if (loading) {
    return (
      <div className="App">
        <p>LOADING...</p>
      </div>
    );
  }

  return (
    <div className="page main">
      <p>Profile page</p>
    </div>
  );
}

export {ProfilePage};
