import React, {useEffect, useState} from "react";
import {Outlet, useNavigate} from "react-router-dom";
import {BottomNavigation, BottomNavigationAction} from "@mui/material";
import HomeRoundedIcon from '@mui/icons-material/HomeRounded';
import QrCodeRoundedIcon from '@mui/icons-material/QrCodeRounded';
import PersonRoundedIcon from '@mui/icons-material/PersonRounded';
import {Html5Qrcode} from "html5-qrcode";

const Layout = () => {
  const navigate = useNavigate();

  const [value, setValue] = useState('main');
  const [code, setCode] = useState('');
  const [loading, setLoading] = useState(true);
  const [scanned, setScanned] = useState(false);
  const [qrCodeReader, setQrCodeReader] = useState<any>();

  const handleChange = (event: React.SyntheticEvent, newValue: string) => {
    setValue(newValue);
  };

  function onScanSuccess(decodedText: any, decodedResult: any) {
    qrCodeReader && qrCodeReader.stop().then(() => {
      setCode(decodedText);
      setScanned(true);
      navigate('result');
    });
    console.log(`Code scanned = ${decodedText}`, decodedResult);
  }
  function onScanFailure(error: any) {
    console.warn(`Code scan error = ${error}`);
  }

  useEffect(() => {
    if (value === 'scan') {
      navigate(value);
      const config: any = {fps: 10, qrbox: 250};
      const html5QrCode = new Html5Qrcode("reader");
      setQrCodeReader(html5QrCode);
      html5QrCode.start({facingMode: "environment"}, config, onScanSuccess, onScanFailure).then(() => {
        setLoading(false);
      });
    } else if (value === 'main') {
      navigate('/');
      qrCodeReader && qrCodeReader.isScanning && qrCodeReader.stop().then((ignore: any) => {
        console.log({ignore});
      }).catch((err: any) => {
        console.log({err});
      });
    } else {
      navigate(value);
      qrCodeReader && qrCodeReader.isScanning && qrCodeReader.stop().then((ignore: any) => {
        console.log({ignore});
      }).catch((err: any) => {
        console.log({err});
      });
    }
  }, [value])

  return (
    <main>
      <Outlet />
      <div
        id="reader"
        className={'reader'}
        style={{width: '100%', display: value === 'scan' ? 'block' : 'none'}}
      />
      <div className={'bottom-nav'}>
        <BottomNavigation showLabels value={value} onChange={handleChange}>
          <BottomNavigationAction
            label=""
            value="main"
            icon={<HomeRoundedIcon />}
          />
          <BottomNavigationAction
            label="QR "
            value="scan"
            icon={<QrCodeRoundedIcon />}
          />
          <BottomNavigationAction
            label=""
            value="profile"
            icon={<PersonRoundedIcon />}
          />
        </BottomNavigation>
      </div>
    </main>
  )
}

export {Layout};
